import React from "react"
import { graphql } from "gatsby"
import Image from "gatsby-image"
import styled from "@emotion/styled"

import Layout from "../Layout/Layout"

export const query = graphql`
  query($slug: String!) {
    allDatoCmsProject(filter: { slug: { eq: $slug } }) {
      nodes {
        titulo
        contenido
        tecnologias
        link
        image {
          fluid(maxWidth: 1200) {
            ...GatsbyDatoCmsFluid
          }
        }
      }
    }
  }
`

const ProjectContainer = styled.div`
  padding: 0 10px;
  text-align: center;
  margin-bottom: 20px;
  h1 {
    text-align: center;
  }
  p,
  legend {
    text-align: justify;
  }
  h2 {
    text-align: left;
  }
  .tech__container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .tech__container p,
  a {
    padding: 5px;
    border-radius: 5px;
    background-color: #0197f6;
    color: var(--white);
  }
  legend {
    margin: 10px 0 20px 0;
    font-style: italic;
    font-size: 1.3rem;
  }
  a {
    text-decoration: none;
  }
  @media (min-width: 767px) {
    .image {
      margin: 10px auto 0;
      width: 80vw;
    }
    .description {
      display: flex;
      justify-content: space-around;
      h2 {
        text-align: center;
      }
      .tech__container p {
        margin-right: 20px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
`

const ProjectDetail = ({
  data: {
    allDatoCmsProject: { nodes },
  },
}) => {
  const {
    titulo,
    contenido,
    image: { fluid },
    tecnologias,
    link,
  } = nodes[0]
  return (
    <Layout>
      <ProjectContainer>
        <Image fluid={fluid} className="image" />
        <h1>{titulo}</h1>
        <p dangerouslySetInnerHTML={{ __html: contenido }} />
        <div className="description">
          <div className="technologies">
            <h2>Tecnologías</h2>
            <div className="tech__container">
              {tecnologias.split(", ").map((tech, index) => (
                <p key={`tech-${index + 1}`}>{tech}</p>
              ))}
            </div>
          </div>
          <div className="demostration">
            <h2>Demo</h2>
            <legend>
              *Algunas demos no permiten registrarse, hablemos para mostrarte
              este proyecto
            </legend>
            <a href={link} target="__blank">
              Clic aquí
            </a>
          </div>
        </div>
      </ProjectContainer>
    </Layout>
  )
}

export default ProjectDetail
